* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}
body {
  width: 100%;
  background-color: rgb(0, 33, 49);
  font-family: "Montserrat";
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.home,
.projects,
.skills,
.contact {
  width: 100vw;
}
.app-container {
  width: 100vw;
}

.homecontainer {
  width: 100vw;
  background-color: rgb(0, 33, 49);
  height: 100vh;
  color: rgb(238, 245, 245);
  display: grid;
  margin: 0 auto;
  grid-template-columns: 1.1fr 0.9fr;
  justify-content: center;
  align-items: center;
}

.namecontainer {
  display: flex;
  flex-direction: column;
  font-size: 1.9rem !important;
  margin-left: 5%;
}
.name {
  margin-left: 35px;
}
.name h1 {
  font-size: 2.9rem;
  font-weight: 5000 !important;
  margin: 10px auto 10px 25px;
}
.name h1 span {
  color: rgb(252, 57, 57);
}
.myphoto-container {
  height: 80%;
  display: flex;
  justify-content: center;
  align-items: center;
}
.myphoto {
  width: 90%;
  height: 60%;
  display: flex;
  justify-content: center;
  align-items: center;
  perspective: 40px;
}
#myphotojpg {
  width: 100%;
  height: auto;
  box-shadow: 0 0px 30px rgba(0, 0, 0, 0.26), 0 00px 40px rgba(0, 0, 0, 0.26),
    0 0px 55px rgba(0, 0, 0, 0.26);
}
@media screen and (max-width: 500px) {
  .myphoto-container {
    visibility: hidden;
  }
}
.innerContainer {
  transition: transform 0.2s;
}

.one,
.two,
.three {
  display: flex;
}
.onel,
.twol,
.threel,
.c {
  margin: 13px 15px 13px 0;
  background-color: white;
}
.onel {
  height: 14px;
  width: 84px;
  background: linear-gradient(to left, #ff6a69 0%, #ffda7a 100%);
}
.twol {
  height: 14px;
  width: 150px;
  background: linear-gradient(to left, #f09fff 0%, #7e99ff 100%);
}
.threel {
  height: 14px;
  width: 250px;
  background: linear-gradient(to left, #44eec7 0%, #6393d6 100%);
}
.c {
  width: 14px;
  height: 14px;
  border-radius: 50%;
  background-color: rgb(150, 150, 150);
}
.top-other,
.bottom-other,
.bottom {
  margin-bottom: 0px;
}

.skills-container {
  background-color: rgb(0, 28, 41);
  height: 100vh;
  color: rgb(238, 245, 245);
}

.skill-container {
  margin: 0 auto;
  max-width: 90%;
  height: 68vh;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  justify-content: center;
  align-items: center;
}
.sheading {
  margin: 0;
  font-size: 60px;
  background-color: rgb(0, 28, 41);
  display: flex;
  justify-content: center;
  align-items: center;
  height: 30vh;
  font-size: 50px;
  font-family: "Dancing Script", cursive;
}
.sheading h1 {
  background: linear-gradient(to left, #f09fff 0%, #7e99ff 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}
.python,
.webdev,
.ccpp {
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: center;
}
.pythondiv,
.webdevdiv,
.cdiv {
  width: 78%;
  display: flex;
  align-items: center;
  justify-content: space-evenly;
}

.list {
  display: flex;
}
.list img {
  width: 6vw;
  height: auto;
}
.list h5 {
  font-size: 1.2rem;
  margin: 5px 0;
}
.skills-container {
  width: 100vw;
}

.projects-container {
  background-color: rgb(0, 33, 49);
  color: rgb(238, 245, 245);
  height: 100vh;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}
.pheading {
  font-size: 2rem;
  margin: 0 auto;
  padding-top: 5%;
  height: 20%;
  font-family: "Dancing Script", cursive;
}
.pheading h1 {
  background: linear-gradient(to left, #ff6a69 0%, #ffda7a 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}
.inner-project-container {
  width: 90%;
  height: 80%;
  margin: 0 auto;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
}
.inner-pcontainer {
  display: flex;
  justify-content: center;
  align-items: center;
}

.procon {
  width: 75%;
  height: 75%;
  margin: 0 auto;
  display: flex;
  justify-content: space-around;
  align-items: center;
  flex-direction: column;
  background-color: #c7dabc;
  color: rgb(3, 8, 26);
  border-radius: 5px;
  box-shadow: 0 0px 30px rgba(0, 0, 0, 0.151), 0 00px 40px rgba(0, 0, 0, 0.26),
    0 0px 55px rgba(0, 0, 0, 0.26);
  transition: width 0.5s, height 0.5s;
}
@media screen and (max-width: 600px) {
  .inner-project-container {
    grid-template-columns: 1fr;
    grid-template-rows: repeat(3, 1fr);
  }
  .procon {
    width: 100%;
    height: 90%;
    flex-direction: row;
  }
  .procon:last-child {
    margin-bottom: 20px;
  }
  .proconimg {
    margin-left: 10px;
  }
}
.procon:hover {
  width: 80%;
  height: 80%;
}
.ptitle {
  width: 100%;
  display: flex;
  align-self: center;
  justify-content: center;
}
.t2 {
  font-family: "Share Tech Mono";
}
.t3 {
  font-family: "Cascadia Mono";
}
.t1,
.t2,
.t3 {
  text-align: center;
  width: 100%;
  font-size: 3vw;
}
.t3 {
  font-size: 2.4vw;
}
.proconimg {
  width: 90%;
  height: auto;
}

.proconimg img {
  width: 100%;
  border-radius: 5px;
}
.pone {
  box-shadow: 0 0px 30px rgba(130, 22, 138, 0.904);
}
.ptwo {
  box-shadow: 0 0px 30px rgb(194, 194, 214);
}
.pthree {
  box-shadow: 0 0px 30px rgb(0, 0, 0);
}
.descc {
  margin: 0 20px;
  text-align: center;
  font-style: italic;
}

.procon a {
  text-decoration: none;
  color: rgb(18, 106, 109);
  font-size: 16px;
  transition: color 0.5s, font-size 0.5s;
}
.procon a:hover {
  color: rgb(255, 89, 89);
  font-size: 18px;
}
/*  */
.projects-container {
  width: 100vw;
}

.concontainer {
  background-color: rgb(0, 28, 41);
  height: 100vh;
  color: aliceblue;
  display: flex;
  align-items: center;
  justify-content: space-evenly;
  flex-direction: column;
}
.cheading {
  text-align: center;
  height: 25vh;
  font-size: 2rem;
}
.cheading h1 {
  background: linear-gradient(to left, #44eec7 0%, #6393d6 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  font-family: "Dancing Script", cursive;
}
.clinks {
  margin-top: -20px;
  width: 80%;
  display: flex;
  justify-content: space-around;
  align-items: center;
}
.clinks a {
  font-size: 3.5vw;
  text-decoration: none;
}
@media screen and (max-width: 904px) {
  .clinks a {
    font-size: 3vw;
  }
}
@media screen and (max-width: 600px) {
  .clinks {
    flex-direction: column;
    height: auto;
  }
  .clinks a {
    font-size: 6vw;
  }
}
.contxt {
  text-align: center;
  font-size: 2rem;
}

.a1 {
  padding: 0.4rem;
  position: relative;
  display: inline-block;
  color: aqua;
  overflow: hidden;
  transition: 0.2s;
}
.a1:hover {
  color: #abd0f0;
  background: #2196f3;
  box-shadow: 0 0 10px #2196f3, 0 0 40px #2196f3, 0 0 60px #2196f3;
  transition-delay: 0.5s;
}
.a1 span {
  position: absolute;
  display: block;
}
.a1 span:nth-child(1) {
  top: 0;
  left: -100%;
  width: 100%;
  height: 2px;
  background: linear-gradient(90deg, transparent, #2196f3);
}
.a1:hover span:nth-child(1) {
  left: 100%;
  transition: 0.5s;
}
.a1 span:nth-child(3) {
  bottom: 0;
  right: -100%;
  width: 100%;
  height: 1px;
  background: linear-gradient(270deg, transparent, #2196f3);
}
.a1:hover span:nth-child(3) {
  right: 100%;
  transition: 0.5s;
  transition-delay: 0.25s;
}
.a1 span:nth-child(2) {
  top: -100%;
  right: 0;
  width: 1px;
  height: 100%;
  background: linear-gradient(180deg, transparent, #2196f3);
}
.a1:hover span:nth-child(2) {
  top: 100%;
  transition: 0.5s;
  transition-delay: 0.125s;
}
.a1 span:nth-child(4) {
  bottom: -100%;
  left: 0;
  width: 2px;
  height: 100%;
  background: linear-gradient(360deg, transparent, #2196f3);
}
.a1:hover span:nth-child(4) {
  bottom: 100%;
  transition: 0.5s;
  transition-delay: 0.375s;
}
.contxt {
  background: linear-gradient(to left, #f09fff 0%, #7e99ff 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}
